/* 登录 */
import './index.less';
import React from 'react';
import { connect } from 'react-redux';
import { Row, Col, Form, Input, Icon, Button, message } from 'antd';

class Login extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err && values.username==='admin' && values.password==='admin') {
        sessionStorage.setItem('usrInfo', JSON.stringify(values));
        this.props.setInfo('usrInfo', values);
      } else {
        message.error('用户名或密码错误');
        sessionStorage.clear();
        this.props.setInfo('usrInfo', null);
      }
    });
  };
  onKeyDown = (e) => {
    switch (e.keyCode) {
      case 13:
        this.handleSubmit(e);
        break;
      default: break;
    }
  };
  routeChange = (info) => {
    let usrInfo = info || JSON.parse(sessionStorage.getItem('usrInfo'));
    if (!window.$globalmethod.isEmpty(usrInfo)) {
      this.props.setInfo('usrInfo', usrInfo);
      this.props.history.replace({pathname: '/layout/chart/2D'});
    }
  };
  constructor(props) {
    super(props);
    this.routeChange(this.props.tiger.usrInfo);
  };
  componentDidMount() {
    document.addEventListener('keydown', this.onKeyDown);
  };
  componentWillUnmount() {
    document.removeEventListener('keydown', this.onKeyDown);
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Row className="login"
        type="flex"
        align="middle"
        justify="center">
        <Col xs={20} sm={10} md={8} lg={6}>
          <Form onSubmit={this.handleSubmit} className="login-form" id="login_tpl">
            <Form.Item className="companyName">
              <i className="fa fa-cloud" aria-hidden="true"></i>react+antd-CMS
            </Form.Item>
            <Form.Item>
              {
                getFieldDecorator('username', {
                  rules: [{ required: true, message: '请输入用户名!' }]
                })(
                  <Input placeholder="账户名"
                    autoComplete="false"
                    prefix={
                      <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />
                    } />
                )
              }
            </Form.Item>
            <Form.Item>
              {
                getFieldDecorator('password', {
                  rules: [{ required: true, message: '请输入密码!' }],
                })(
                  <Input type="password"
                    placeholder="密码"
                    autoComplete="false"
                    prefix={
                      <Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
                    } />
                )
              }
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" className="login-form-button">
                登录
              </Button>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    );
  }
};
const mapStateToProps = (state) => {
  return {
    tiger: state
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    setInfo: (type, params) => dispatch({ type: type, params: params })
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Form.create({name: 'login_tpl'})(Login));
